import { Launch } from "@/launch/launch";
import { Log } from "@/utils/log";
let launch = new Launch();
class CPLASDK {
    static init(initParams) {
        const { callback, game_version, } = initParams;
        Log.commonS('游戏调用sdk初始化开始。');
        launch.externalInit({
            cpSpace: this,
            callback,
            game_version,
        });
    }
}
window.CPLASDK = CPLASDK;
