let glogSign;
let lvanSDKStore = {
//cpsdk_version
//cpsdk_account_id
//pubsdk_version
//pubsdk_account_id
//pubsdk_account_name
};
let otherStore = {};
//初始化
export function glogInit(sign) {
    glogSign = sign;
}
//数据处理
export function glogRequest(eventName, eventData) {
    if (eventName == 'other') {
        let glogJson = {};
        eventData.map((item) => {
            glogJson[item] = getParams(item);
        });
        return glogJson;
    }
    else {
        //读取配置文件
        let glogJson = {};
        let glogList = glogInfo[glogSign + 'glog'];
        glogJson.version = glogList.version;
        glogJson.event = glogSign + 'sdk_' + eventName;
        (['log_id', 'event_time'].concat((glogList[eventName]).split(','))).map((item) => {
            glogJson[item] = getParams(item);
        });
        //缓存游戏数据
        Object.assign(otherStore, eventData);
        //拼接事件数据
        Object.assign(glogJson, eventData);
        upLog([glogJson]);
    }
}
function getParams(event) {
    let eventData = paramsFunnel[event];
    switch (eventData.origin) {
        case 'handler':
            return getDataFormHandler(eventData);
            break;
        case 'sdk':
            return getDataFormSdk(eventData);
            break;
    }
}
function getDataFormHandler(event) {
    if (event) {
        try {
            return handleFn[event.handlerId]();
        }
        catch (e) {
        }
    }
    else {
        return '';
    }
}
function getDataFormSdk(event) {
    if (event) {
        if (event.paramId) {
            return lvanSDKStore[event.paramId];
        }
        else {
            return event.default;
        }
    }
    else {
        return '';
    }
}
export function setPublicParams(glog) {
    Object.assign(lvanSDKStore, glog);
}
let timerHeart;
function upLog(glog) {
    Request.create({
        // url:process.env.SDK_cpGlogUrl+'/'+glogInfo[glogSign+'glog'].glog_key,
        url: process.env.SDK_cpGlogUrl,
        data: glog,
        type: 'POST',
        ext: {
            notX8: true
        }
    }).then((res) => {
        //判断是否成功
        if (res.code != 0) {
            if (glog.event.indexOf('heartbeat') != -1) {
                //入备份库
                addFailLog(glog);
            }
        }
        if (!timerHeart && glog[0].event.indexOf('role_login') != -1) {
            // timerHeart&&clearInterval(timerHeart);
            timerHeart = setInterval(() => {
                glogRequest('heartbeat', otherStore);
            }, 300000);
        }
    });
}
//添加失败日志
function addFailLog(log) {
    let logList = localStorage.getItem(glogSign + 'sdk_log') ? JSON.parse(localStorage.getItem(glogSign + 'sdk_log')) : [];
    localStorage.setItem(glogSign + 'sdk_log', JSON.stringify(logList.concat(log)));
    timer || openUpFailGlog();
}
//开启日志库上报
let timer;
function openUpFailGlog() {
    timer = setTimeout(() => {
        //获取失败日志
        let logList = localStorage.getItem(glogSign + 'sdk_log') ? JSON.parse(localStorage.getItem(glogSign + 'sdk_log')) : [];
        if (logList.length > 0) {
            upLog(logList);
        }
        timer = null;
    }, 300000);
}
