let publisher = require('@/publisher/' + process.env.SDK_pubCode).Publisher;
let publisherConfig = require('@/publisher/' + process.env.SDK_pubCode + '/config').config;
import { loadScript, getAction } from '@/utils/tools';
import { Request } from '@/utils/request';
import { getDeviceId } from '@/utils/deviceId';
import { glogInit, glogRequest, setPublicParams } from "@/utils/glog/index";
import { Log } from "@/utils/log";
import { errorHandlerCP, warnHandlerCP } from '@/utils/errorHandler';
import { CP_CODE } from '@/type/code';
const packageJson = require('../../package.json');
const GLOG_SIGN = 'cp';
export class Work {
    /** 内部初始化 */
    insideInit(callback) {
        Log.commonS('h5work初始化开始。');
        //加载发行资源
        let publisherFileLoad = this.loadPublisherInfo();
        glogInit(GLOG_SIGN);
        publisherFileLoad.then(() => {
            //设置sdk版本
            setPublicParams({
                'cpsdk_version': packageJson.version
            });
            //上报启动/激活
            glogRequest('active', {
                is_first_active: getAction(),
            });
            //回调初始化完成
            callback({
                cpSpace: this.getSpaceFunction()
            });
            Log.commonS('h5work初始化完成。');
        }).catch((e) => {
            errorHandlerCP(CP_CODE.INSIDE_H5_INIT_PUB_RES_LOAD_FAIL, e);
        });
    }
    loadPublisherInfo() {
        Log.commonS('发行资源加载开始。');
        return new Promise((resolve, reject) => {
            Promise.all([
                this.loadCpSDKFile(publisher),
                this.loadPublisherSDKFile(publisherConfig.getSdkSign, publisherConfig.sdkType)
            ]).then((res) => {
                Log.commonS('发行资源加载结束。');
                this.publisherSDk = new res[0]();
                resolve(true);
            }).catch((e) => {
                reject(e);
            });
        });
    }
    loadCpSDKFile(publisher) {
        Log.commonS('发行实现加载开始');
        return new Promise((resolve, reject) => {
            Log.commonS('发行实现加载完成');
            if (publisher) {
                resolve(publisher);
            }
            else {
                errorHandlerCP(CP_CODE.INSIDE_H5_INIT_PUB_ACHIEVE_RES_LOAD_FAIL);
                reject({
                    e: new Error('发行实现缺失'),
                    pubCOde: process.env.SDK_pubCode
                });
            }
        });
    }
    loadPublisherSDKFile(publisherSdkPath, type) {
        Log.commonS('发行渠道sdk加载开始。');
        return new Promise((resolve, reject) => {
            if (type == 'script') {
                Log.commonS('发行渠道sdk script模式。');
                const sdkScript = document.createElement('script');
                window.sdkLoadCallback = () => {
                    Log.commonS('发行渠道sdk加载完成。');
                    resolve(true);
                };
                window.sdkLoadErrorBack = (e) => {
                    errorHandlerCP(CP_CODE.INSIDE_H5_INIT_PUB_SDK_RES_LOAD_FAIL, e);
                    reject(e);
                };
                sdkScript.textContent = publisherSdkPath;
                let b = document.getElementsByTagName("script")[0];
                b.parentNode.insertBefore(sdkScript, b);
            }
            else {
                Log.commonS('发行渠道sdk src模式。');
                loadScript(publisherSdkPath, () => {
                    resolve(true);
                }, (e) => {
                    errorHandlerCP(CP_CODE.INSIDE_H5_INIT_PUB_SDK_RES_LOAD_FAIL, e);
                    reject(e);
                }, type);
            }
        });
    }
    /** 初始化流程 */
    init(params) {
        Log.commonS('h5work 初始化开始');
        const { callback } = params;
        //判断发行是否存在初始化流程
        if (this.publisherSDk.init) {
            //执行发行初始化
            this.noticePushlierInit(params);
        }
        else {
            //初始化成功
            Log.commonS('h5work 初始化完成');
            callback(Object.assign({}, errorHandlerCP(CP_CODE.SUCCESS)));
        }
        return;
    }
    ;
    noticePushlierInit(params) {
        Log.commonS('h5work发行初始化开始');
        const { callback, game_version } = params;
        this.publisherSDk.init({
            game_version
        }).then((res) => {
            let resultData = Object.assign({}, errorHandlerCP(CP_CODE.SUCCESS, 'h5work发行初始化完成'));
            if (res.data)
                resultData.data = res.data;
            callback(resultData);
        }).catch((e) => {
            callback(Object.assign({}, errorHandlerCP(CP_CODE.H5_WORK_PUB_INIT_ERROR, e)));
        });
    }
    /** 登录 */
    login(params) {
        Log.commonS('h5work发行登录开始');
        const { callback } = params;
        this.publisherSDk.login().then((res) => {
            Log.commonS('h5work发行登录完成');
            if (res.code == 200) {
                this.insideLogin(res.data, callback);
            }
        }).catch((res) => {
            callback(Object.assign({}, errorHandlerCP(CP_CODE.H5_WORK_PUB_LOGIN_ERROR)));
        });
    }
    /** 内部登录 */
    insideLogin(token, callback) {
        Log.commonS('h5work内部登录开始');
        Request.create({
            url: process.env.SDK_cpSDKApiUrl + '/sdk/client/login',
            data: {
                zoneId: process.env.SDK_zoneId,
                tunnelId: process.env.SDK_tunnelId,
                pubId: process.env.SDK_pubId,
                gameId: process.env.SDK_gameId,
                pubTokenInfo: JSON.stringify(Object.assign({}, token)),
                deviceEnv: this.getDeviceEnv()
            },
            type: 'POST',
        }).then((res) => {
            if (res.code == 0) {
                setPublicParams({
                    'cpsdk_account_id': res.data.cpsdkAccountId,
                });
                callback(Object.assign(Object.assign({}, errorHandlerCP(CP_CODE.SUCCESS, 'h5work内部登录成功')), { data: {
                        cpsdkToken: res.data.cpsdkToken,
                        tunnelId: res.data.tunnelId,
                        zoneId: res.data.zoneId
                    } }));
            }
            else {
                callback(Object.assign({}, errorHandlerCP(CP_CODE.H5_WORK_LOGIN_ERROR, res)));
            }
        }).catch((e) => {
            callback(Object.assign({}, errorHandlerCP(CP_CODE.NETERROR, e)));
        });
    }
    /** 支付 */
    pay(params) {
        Log.commonS('h5work发行支付拉起开始');
        this.publisherSDk.pay(params);
        //上报支付
        glogRequest('pay_order_client', params);
    }
    getDeviceEnvOutSide(params) {
        Log.commonS('h5work环境信息获取开始');
        const { callback } = params;
        callback && callback(this.getDeviceEnv());
    }
    /** 获取设备信息 */
    getDeviceEnv() {
        return window.btoa(JSON.stringify({
            env: Object.assign({ os: navigator.platform, deviceId: getDeviceId(), serial: '', imei: '', androidId: '', oaid: '', idfa: '', ip: '', brand: '', mobileModel: '' }, glogRequest('other', ['device_id', 'platform', 'app_version', 'cpsdk_version', 'pubsdk_version'])),
            pub: Object.assign({}, glogRequest('other', ['channel_id']))
        }));
    }
    /** 登出 */
    logout() {
        Log.commonS('h5work发行登出开始');
        this.publisherSDk.logout();
    }
    ;
    /** 切换账号 */
    switchAccount(params) {
        Log.commonS('h5work发行切换账号开始');
        const { callback } = params;
        if (this.publisherSDk.switchAccount) {
            this.publisherSDk.switchAccount().then((res) => {
                if (res.code == 200) {
                    this.insideLogin(res.data, callback);
                }
                // callback(res);
            });
        }
        else {
            this.publisherSDk.logout();
        }
    }
    /** 主动分享 */
    activeShare() {
        Log.warn(warnHandlerCP(CP_CODE.FUNCTION_IS_NOT_ACHIEVE_BY_CP_WORK));
    }
    ;
    onEventLog({ eventCode, eventData }) {
        Log.commonS('数据上报开始', eventCode, eventData);
        glogRequest(eventCode, eventData);
        this.publisherSDk.report && this.publisherSDk.report(eventCode, eventData);
    }
    getSpaceFunction() {
        return {
            init: this.init.bind(this),
            login: this.login.bind(this),
            pay: this.pay.bind(this),
            logout: this.logout.bind(this),
            switchAccount: this.switchAccount.bind(this),
            getDeviceEnv: this.getDeviceEnvOutSide.bind(this),
            onEventLog: this.onEventLog.bind(this),
        };
    }
}
