import { CP_CODE } from "@/type/code";
import { Log } from "./log";
export function errorHandlerCP(status, ...msg) {
    if (status.code == CP_CODE.SUCCESS.code) {
        msg.length > 0 && Log.commonS('[cp work]', ...msg);
    }
    else {
        //输出错误工作状态
        Log.error(status.msg, '[cp work][code:]' + status.code, ...msg);
    }
    //返回工作状态
    return Object.assign({}, status);
}
export function warnHandlerCP(status, ...msg) {
    Log.warn(status.msg, '[cp work][code:]' + status.code, ...msg);
}
