import { ENV } from "@/type/public";
import { GetUrlParams } from "./getUrlParams";
import { getNowMicroseconds } from "./tools";
export class Log {
    /**
     * 普通日志
     * 打包抹除
     */
    static common(...args) {
        if (process.env.SDK_env != ENV.master)
            console.log('[cpsdk][debugger log]:', ...args);
    }
    /**
     * s级系统日志
     * 打包后不丢失
     * */
    static commonS(...args) {
        if (process.env.SDK_env != ENV.master) {
            console.log('[cpsdk][info]:', ...args);
        }
        else {
            (GetUrlParams.getInstance().get('logDebug') == 'debug') && console.log('[cpsdk][info]:', ...args);
        }
    }
    /**
     * 错误日志
     * 打包时不再输入改为上报
     * */
    static error(...args) {
        //TODO 策略上报
        if (process.env.SDK_env != ENV.master)
            console.error('[cpsdk][error]:', ...args);
    }
    /**
     * 警告日志
     * 打包抹除
     * */
    static warn(...args) {
        if (process.env.SDK_env != ENV.master)
            console.warn('[cpsdk][warnning]:', ...args);
    }
    /**
     * 封装时间差开始组件
     * 打包抹除
     * */
    static timeStart(logName) {
        if (process.env.SDK_env != ENV.master) {
            if (this.logTimeState.hasOwnProperty(logName))
                this.error(`logName ${logName} is exist`);
            this.logTimeState[logName] = getNowMicroseconds();
            console.log(`[cpsdk][timer]:start ${logName}`);
            return logName;
        }
    }
    /**
     * 封装时间差结束组件
     * 打包抹除
     *  */
    static timeEnd(logName) {
        if (process.env.SDK_env != ENV.master) {
            if (!this.logTimeState.hasOwnProperty(logName))
                this.error(`logName ${logName} is not exist`);
            const logTime = getNowMicroseconds() - this.logTimeState[logName];
            console.log(`[cpsdk][timer]:end ${logName} --${logTime}μs`);
            return logTime;
        }
    }
}
Log.logTimeState = {};
