import { Log } from "@/utils/log";
import { Device as DeviceH5 } from '@/devices/h5';
import { Device as DeviceAndroid } from '@/devices/android';
import { Device as DeviceIos } from '@/devices/ios';
import { errorHandlerCP } from "@/utils/errorHandler";
import { CP_CODE } from "@/type/code";
/** 启动 */
export class Launch {
    constructor() {
        /** 内部初始化状态
         * true 初始化完成
         * false 初始化中
        */
        this.insideInitState = false;
        /** 外部初始化状态
         * true 已进行
         * false 未进行
         */
        this.externalInitState = false;
        Log.timeStart('sdk_init_inside');
        Log.commonS('sdk内部初始化开始。');
        this.init();
    }
    /** 内部初始化 */
    init() {
        let initPromise;
        if (process.env.SDK_shell == 'h5') {
            initPromise = this.initH5();
        }
        else if (process.env.SDK_shell == 'android') {
            initPromise = this.initAndroid();
        }
        else if (process.env.SDK_shell == 'ios') {
            initPromise = this.initIos();
        }
        initPromise.then(({ cpSpace }) => {
            this.judgeExternalInitState(cpSpace);
            Log.commonS('sdk内部初始化结束。');
            Log.timeEnd('sdk_init_inside');
        }).catch(() => {
            errorHandlerCP(CP_CODE.INSIDE_INIT_ERROR);
        });
    }
    initIos() {
        if (process.env.SDK_shell == 'ios') {
            Log.commonS('ios初始化开始。');
            return new Promise((revlose) => {
                DeviceIos.getInstance().init().then((params) => {
                    Log.commonS('ios初始化成功。');
                    revlose(params);
                }).catch(() => {
                    errorHandlerCP(CP_CODE.INSIDE_IOS_INIT_ERROR);
                });
            });
        }
    }
    initAndroid() {
        if (process.env.SDK_shell == 'android') {
            Log.commonS('android初始化开始。');
            return new Promise((revlose) => {
                DeviceAndroid.getInstance().init().then((params) => {
                    Log.commonS('android初始化成功。');
                    revlose(params);
                }).catch(() => {
                    errorHandlerCP(CP_CODE.INSIDE_ANDROID_INIT_ERROR);
                });
            });
        }
    }
    initH5() {
        //拉取h5配置
        if (process.env.SDK_shell == 'h5') {
            Log.commonS('h5初始化开始。');
            return new Promise((revlose) => {
                DeviceH5.getInstance().init().then((params) => {
                    Log.commonS('h5初始化成功。');
                    revlose(params);
                }).catch(() => {
                    errorHandlerCP(CP_CODE.INSIDE_H5_INIT_ERROR);
                });
            });
        }
    }
    /** 外部初始化 */
    externalInit(insideInitParams) {
        const { cpSpace, } = insideInitParams;
        //判断内部初始化状态
        //初始化参数插入
        //激活日志上报
        if (this.insideInitState) {
            Log.commonS('game-cp 内部初始化已完成');
            Object.assign(cpSpace, this.insideInitResult.cpSpace);
            //执行初始化
            this.insideInitResult.cpSpace.init(insideInitParams);
        }
        else {
            Log.commonS('game-cp 内部初始化未完成');
            this.externalInitState = true;
            this.externalInitParams = insideInitParams;
            this.cpSpace = cpSpace;
        }
    }
    /**  */
    /** 判断外部初始化状态 */
    judgeExternalInitState(cpSpace) {
        if (this.externalInitState) {
            Log.commonS('cp-game 外部初始化已执行');
            Object.assign(this.cpSpace, cpSpace);
            cpSpace.init(this.externalInitParams);
        }
        else {
            Log.commonS('cp-game 等待外部初始化开始');
            this.insideInitState = true;
            this.insideInitResult = {
                cpSpace
            };
        }
    }
}
