import { Md5 } from '@/utils/md5';
export function getDeviceId() {
    return window.localStorage.getItem("lvdid") || createDeviceId();
}
/**
 * @description: 创建设备id
 * @param {*}
 * @return {*}
 */
function createDeviceId() {
    let useragent = navigator.userAgent.toLocaleLowerCase();
    let _did = 'cd_' + Md5.hashStr(useragent + (new Date().getTime() + Math.floor(Math.random() * 10000) / 10000));
    window.localStorage.setItem("lvdid", _did);
    return _did;
}
