/** 解析get类型数据 */
export const analysisGet = (parsedData) => {
    let jsonData = {};
    //乾坤大挪移
    parsedData.split('&').map((res) => {
        let data = res.split('=');
        jsonData[data[0]] = data[1];
    });
    return jsonData;
};
/** 获取当前微秒 */
export function getNowMicroseconds() {
    var start = performance.timing ?
        performance.timing.navigationStart :
        performance.timeOrigin;
    return (performance.now() + start) * 1000;
}
/** 加载script */
export function loadScript(url, callback, errorBack, type) {
    if (document.body) {
        let script = document.createElement('script');
        script.src = url;
        script.onload = callback;
        script.onerror = errorBack;
        type && (script.type = type);
        document.body.append(script);
    }
    else {
        document.write(`<script src="${url}" onload="${callback()}"><\/script>`);
    }
}
/** 拼接get */
export function joinGetParams(params) {
    let data = '';
    for (let i in params) {
        data += `${i}=${params[i]}&`;
    }
    return data.slice(0, data.length - 1);
}
/** 补零 */
export function zeroFill(number, digit) {
    if ((number + '').length >= digit)
        return number + '';
    return (new Array(digit + 1 - (number + '').length)).join('0') + number;
}
/** 获取时区 */
export function getUTC() {
    return '+' + zeroFill((0 - new Date().getTimezoneOffset() / 60), 2) + '00';
}
/** 获取时间格式 */
export function getDateFormat(time, format = 'year-month-day hour:min:sec') {
    let date;
    //判断格式
    if (typeof time != 'object') {
        date = new Date(time);
    }
    else {
        date = time;
    }
    const year = date.getFullYear() + '';
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const hour = date.getHours();
    const min = date.getMinutes();
    const sec = date.getSeconds();
    return format.replace('year', year)
        .replace('month', zeroFill(month, 2) + '')
        .replace('day', zeroFill(day, 2) + '')
        .replace('hour', zeroFill(hour, 2) + '')
        .replace('min', zeroFill(min, 2) + '')
        .replace('sec', zeroFill(sec, 2) + '');
}
export function getOsVersion() {
    var u = navigator.userAgent, version = '';
    if (u.indexOf('Mac OS X') > -1) {
        // ios
        var regStr_saf = /OS [\d._]*/gi;
        var verinfo = u.match(regStr_saf);
        version = 'IOS ' + (verinfo + '').replace(/[^0-9|_.]/ig, '').replace(/_/ig, '.');
    }
    else if (u.indexOf('Android') > -1 ||
        u.indexOf('Linux') > -1) {
        // android
        version = 'Android ' + u.substr(u.indexOf('Android') + 8, u.indexOf(';', u.indexOf('Android')) - u.indexOf('Android') - 8);
    }
    else if (u.indexOf('BB10') > -1) {
        // 黑莓bb10系统
        version = 'blackberry ' + u.substr(u.indexOf('BB10') + 5, u.indexOf(';', u.indexOf('BB10')) - u.indexOf('BB10') - 5);
    }
    else if (u.indexOf('IEMobile') > -1) {
        // windows phone
        version = 'winphone ' + u.substr(u.indexOf('IEMobile') + 9, u.indexOf(';', u.indexOf('IEMobile')) - u.indexOf('IEMobile') - 9);
    }
    else {
        var userAgent = navigator.userAgent.toLowerCase();
        if (userAgent.indexOf('windows nt 5.0') > -1) {
            version = 'Windows 2000';
        }
        else if (userAgent.indexOf('windows nt 5.1') > -1 || userAgent.indexOf('windows nt 5.2') > -1) {
            version = 'Windows XP';
        }
        else if (userAgent.indexOf('windows nt 6.0') > -1) {
            version = 'Windows Vista';
        }
        else if (userAgent.indexOf('windows nt 6.1') > -1 || userAgent.indexOf('windows 7') > -1) {
            version = 'Windows 7';
        }
        else if (userAgent.indexOf('windows nt 6.2') > -1 || userAgent.indexOf('windows 8') > -1) {
            version = 'Windows 8';
        }
        else if (userAgent.indexOf('windows nt 6.3') > -1) {
            version = 'Windows 8.1';
        }
        else if (userAgent.indexOf('windows nt 6.2') > -1 || userAgent.indexOf('windows nt 10.0') > -1) {
            version = 'Windows 10';
        }
        else {
            version = 'Unknown';
        }
    }
    return version.toLocaleLowerCase();
}
//记录客户端激活参数
export function getAction() {
    let first_active = localStorage.getItem('cp_unFirstActive') ? 0 : 1;
    localStorage.setItem('cp_unFirstActive', '1');
    return first_active + '';
}
