import { joinGetParams } from '@/utils/tools';
export class Request {
    /** get请求 */
    static requestGet(params) {
        const { url, data = {}, success = null, fail = null, header = {} } = params;
        let _data = joinGetParams(data);
        var xhr = new XMLHttpRequest();
        xhr.open('GET', url + "?" + decodeURIComponent(_data), true);
        xhr.onreadystatechange = function () {
            let _datas = null;
            if (xhr.readyState == 4 && xhr.status == 200 || xhr.status == 304) {
                try {
                    _datas = typeof xhr.responseText == "string" ? JSON.parse(xhr.responseText) : xhr.responseText;
                }
                catch (e) {
                    _datas = xhr.responseText;
                }
                success && success.call(this, _datas);
            }
            else if (xhr.status && xhr.status != 200) {
                //TODO 统一错误处理
                fail && fail.call(this, _datas);
            }
        };
        xhr.send();
    }
    /** post请求 */
    static requestPost(params) {
        const { url, data = {}, success = null, fail = null, header = {}, ext = {} } = params;
        var xhr = new XMLHttpRequest();
        xhr.open("POST", url, true);
        if (!ext.notX8) {
            xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        }
        for (const i in header) {
            xhr.setRequestHeader(i, header[i]);
        }
        xhr.onreadystatechange = function () {
            let _datas = null;
            if (xhr.readyState == 4 && xhr.status == 200 || xhr.status == 304) {
                try {
                    _datas = typeof xhr.responseText == "string" ? JSON.parse(xhr.responseText) : xhr.responseText;
                }
                catch (e) {
                    _datas = xhr.responseText;
                }
                success && success.call(this, _datas);
            }
            else if (xhr.status && xhr.status != 200) {
                //TODO 统一错误处理
                fail && fail.call(this, _datas);
            }
        };
        xhr.send(JSON.stringify(data));
    }
    /** 请求 */
    static create(params) {
        const { url, type = 'get', data = {}, header = {}, ext = {} } = params;
        // let x8Config = DataManage.getInstance().getSpaceData({
        //     spaceName:'sdk',
        // });
        // let x8RequestUrl = x8Config.x8_request_url;
        // let x8ReportUrl = x8Config.x8_request_url;
        // let postUrl = '';
        // if(url.indexOf('/x8request')!=-1){
        //     postUrl = url.replace('/x8request',x8RequestUrl)
        // }else if(url.indexOf('/x8rpt')!=-1){
        //     postUrl = url.replace('/x8report',x8ReportUrl)
        // }
        return new Promise((resolve, reject) => {
            switch (type === null || type === void 0 ? void 0 : type.toLowerCase()) {
                case 'get':
                    this.requestGet({
                        url,
                        data,
                        header,
                        ext,
                        success: (res) => {
                            resolve(res);
                        },
                        fail: (res) => {
                            reject(res);
                        }
                    });
                    break;
                case 'post':
                    this.requestPost({
                        url,
                        data,
                        header,
                        ext,
                        success: (res) => {
                            resolve(res);
                        },
                        fail: (res) => {
                            reject(res);
                        }
                    });
                    break;
            }
        });
    }
}
