export class Publisher {
    constructor() {
        this.sdkScript = `(function() {  var s = new URLSearchParams(location.search);s = s.get('pubPath')+'/index.js?timestamp='+(+new Date()); var v = document.createElement('script');v.src = s;v.type = 'module';v.onload = window.sdkLoadCallback;v.onerror = window.sdkLoadErrorBack; var b = document.getElementsByTagName("script")[0];b.parentNode.insertBefore(v, b);})();`;
        this.sdkType = "script";
        this.publisherFuncList = {
            "init": window.PUBLASDK.init,
            "login": window.PUBLASDK.login,
            "pay": window.PUBLASDK.pay,
            "logout": window.PUBLASDK.logout,
            "onEventLog": window.PUBLASDK.onEventLog,
        };
    }
    /** 初始化 */
    init(data) {
        const { game_version, appkey } = data;
        return new Promise((resolve, reject) => {
            this.publisherFuncList.init({
                game_version,
                callback: (res) => {
                    if (res.code == 200) {
                        resolve(true);
                    }
                    else {
                        reject(res);
                    }
                }
            });
        });
    }
    /** 登录 */
    login() {
        return new Promise((resolve, reject) => {
            this.publisherFuncList.login({
                callback: (res) => {
                    if (res.code == 200) {
                        resolve({
                            code: 200,
                            data: {
                                pubsdkToken: res.data.token,
                            }
                        });
                    }
                }
            });
        });
    }
    /** 支付 */
    pay(params) {
        const { order_amount, order_moneytype = 'CNY', zone_id, server_id, server_name, gameVersion, role_id, role_name, role_sex, role_prof, role_level, role_vip_level, role_reborn_level, role_combat, map_id, money_balance, bmoney_balance, gold_balance, online_time, user_ip, extension, } = params;
        return new Promise((resolve, reject) => {
            this.publisherFuncList.pay({
                order_amount,
                order_moneytype,
                zone_id,
                server_id,
                server_name,
                gameVersion,
                role_id,
                role_name,
                role_sex,
                role_prof,
                role_level,
                role_vip_level,
                role_reborn_level,
                role_combat,
                map_id,
                money_balance,
                bmoney_balance,
                gold_balance,
                online_time,
                user_ip,
                extension,
                callback: (res) => {
                    resolve(true);
                }
            });
        });
    }
    /** 登出 */
    logout() {
        this.publisherFuncList.logout({
            callback: () => {
            }
        });
        window.location.reload();
    }
    report(eventCode, eventData) {
        this.publisherFuncList.onEventLog({ eventCode, eventData });
    }
}
