import { analysisGet } from '@/utils/tools';
/** url地址参数处理 */
export class GetUrlParams {
    constructor() {
        this.urlParams = this.analysis();
    }
    static getInstance() {
        return this.instance;
    }
    analysis() {
        let urlData = location.search.slice(1, location.search.length);
        return analysisGet(urlData);
    }
    /** 获取单数据 */
    get(key) {
        let params = this.urlParams[key];
        return params;
    }
    /** 获取所有数据 */
    getAll() {
        return this.urlParams;
    }
    /** 是否为android */
    getIsAndroid() {
        return Boolean(GetUrlParams.getInstance().get('android'));
    }
    /** 是否为ios */
    getIsIos() {
        return Boolean(GetUrlParams.getInstance().get('ios'));
    }
    /** 是否为app */
    getIsApp() {
        return Boolean(GetUrlParams.getInstance().get('isApp'));
    }
    /** 获取发行商code */
    getPubisherId() {
        //TODO 
        return GetUrlParams.getInstance().get('pubId');
    }
    /** 获取发行商code */
    getPubisherCode() {
        //TODO 
        return GetUrlParams.getInstance().get('pubCode');
    }
    /** gameid */
    getGameId() {
        //TODO 
        return GetUrlParams.getInstance().get('gameId');
    }
    /** zoneId */
    getZoneId() {
        //TODO
        return GetUrlParams.getInstance().get('zoneId');
    }
    /** tunnelId */
    getTunnelId() {
        //TODO 
        return GetUrlParams.getInstance().get('tunnelId');
    }
}
GetUrlParams.instance = new GetUrlParams();
